*[hidefocus]{outline:none;}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,form,fieldset,input,textarea,p,blockquote,th,td{padding:0;margin:0;}
fieldset,img,html,body,iframe{border:0;}
table{border-collapse:collapse;border-spacing:0;}
li{list-style:none;}
caption,th{font-weight:normal;font-style:normal;text-align:left;}
em{font-style:normal;}
strong{font-weight:bold;}
body,textarea,select,input{font-family: "Arial","Hiragino Sans GB", \5fae\8f6f\96c5\9ed1, "Helvetica", "sans-serif"; font-size:14px; line-height: 21px; color:#343d42;outline:none; border: 0;}
textarea{resize:none;}
input{vertical-align: baseline; margin: 0px;}
body{background-color:#fff;}
h1,h2,h3,h4,h5,h6{font-weight:normal;}
a,button{cursor:pointer;}
html, body { width: 100%; height: 100%; }
html { overflow:auto; }
body { text-align: left; background:#fff; }
textarea {overflow: auto} //ie8


::-ms-clear,::-ms-reveal{
    display:none;
}
